import React, { useCallback, useEffect, useState } from 'react';
import { Scrollbars } from 'react-custom-scrollbars-2';
import { ScrollZone } from '@pages/workspace/Analyze/styles';
import {
  Content,
  ContentLine,
  ContentLineText,
  CustomIconButton,
  CustomTextButton,
  SelectOption,
} from '@components/Playbar/styles';
import '../../../index.css';
import { Coordinate, IRoute, NullableIGate } from '@typings/db';
import { useParams } from 'react-router';
import useCustomGates from '@hooks/useCustomGates';
import useCustomWorkspace from '@hooks/useCustomWorkspace';
import { toast } from 'react-toastify';
import Sortable from 'sortablejs';
import EachGate from '@components/Analyze/EachGate';

const GateList = () => {
  const { gatesState, updateGatesHook } = useCustomGates();
  const [updateGatesState, setUpdateGatesState] = useState<NullableIGate[]>([]);
  const { workspaceState, postAddRouteAsyncHook, putRouteAsyncHook, putRoutesAsyncHook } = useCustomWorkspace();
  const { route, routes } = workspaceState;
  const [updateRouteState, setUpdateRouteState] = useState<IRoute>(workspaceState.route);
  const [updateRoutesState, setUpdateRoutesState] = useState<IRoute[]>(workspaceState.routes);
  const [isModify, setIsModify] = useState<boolean>(false);
  const { url } = useParams<{ url: string }>();

  useEffect(() => {
    setUpdateGatesState(gatesState);
  }, [gatesState]);

  useEffect(() => {
    setUpdateRouteState(route);
  }, [route]);

  useEffect(() => {
    setUpdateRoutesState(routes);
  }, [routes]);

  // useEffect(() => {
  //   console.log('updateRouteState?.id', updateRouteState?.id);
  // }, [updateRouteState]);

  useEffect(() => {
    // input 으로 updateGatesState 를 변경할 때마다 updateRouteState, updateRoutesState 변경
    changeRouteRoutesWithGates(updateGatesState);
  }, [updateGatesState]);

  // sortable useEffect
  useEffect(() => {
    const sortableElement = document.getElementById('sortable-gates');
    if (sortableElement && isModify) {
      const sortable = Sortable.create(sortableElement, {
        onEnd: (evt) => {
          const reorderedGates = [...updateGatesState];
          if (evt.oldIndex != null && evt.newIndex != null) {
            const [movedItem] = reorderedGates.splice(evt.oldIndex, 1);
            reorderedGates.splice(evt.newIndex, 0, movedItem);

            const reindexedGates = reorderedGates.map((gate, index) => ({
              ...gate,
              sequence: index,
            }));
            setUpdateGatesState(reindexedGates);
          }
        },
      });
      return () => {
        sortable.destroy(); // 컴포넌트 언마운트 시 destroy 로 정리
      };
    }
  }, [isModify, updateGatesState]);

  const onChangeToModify = () => {
    setIsModify(true);
  };

  // save 버튼 click
  const onSaveGateList = useCallback(async () => {
    if (url) {
      try {
        console.log('save : ', updateGatesState);
        await updateGatesHook(url, updateGatesState);
        await putRouteAsyncHook(url, updateRouteState);
        await putRoutesAsyncHook(url, updateRoutesState);
        setIsModify(false);
        toast.dismiss();
        toast.success('The gates have been successfully updated');
      } catch (error) {
        toast.error('Failed to update the gates');
      }
    }
  }, [updateGatesHook, putRouteAsyncHook, putRoutesAsyncHook, url, updateGatesState, route, routes]);

  // input 으로 updateGateState 변경
  const handleGateChange = (index: number, updatedGate: NullableIGate) => {
    setUpdateGatesState((prev) => prev.map((gate, i) => (i === index ? updatedGate : gate)));
  };

  // route SelectOption 변경
  const changeRoute = async (e: React.ChangeEvent<HTMLSelectElement>) => {
    const selectedValue = e.target.value;

    if (selectedValue === 'createRoute' && url) {
      try {
        // 새로운 route 생성 후, postAddRouteAsyncHook 비동기 호출
        const newRoute = await postAddRouteAsyncHook(url, { id: 0, name: 'New Route', coordinates: [] });

        // 비동기 응답 후, workspaceState.route 업데이트
        setUpdateRouteState(newRoute);
        setUpdateRoutesState((prevRoutes) => [...prevRoutes, newRoute]);

        // 새로운 route를 반영한 gates 업데이트
        const newGatesWithSelectedRoute = changeGatesWithRoute(updateGatesState, newRoute);
        setUpdateGatesState(newGatesWithSelectedRoute);

        // workspaceState 갱신
        await putRoutesAsyncHook(url, [...routes, newRoute]);

        toast.dismiss();
        toast.success('New route created and gates updated');
      } catch (error) {
        toast.error('Failed to create route');
        console.error('Error creating new route:', error);
      }
    } else {
      // 기존 route를 선택할 때 처리
      const selectedRoute = routes.find((r) => r.id.toString() === selectedValue);
      if (selectedRoute) {
        setUpdateRouteState(selectedRoute);
        const newGatesWithSelectedRoute = changeGatesWithRoute(updateGatesState, selectedRoute);
        setUpdateGatesState(newGatesWithSelectedRoute);
      }
    }
  };

  // route -> Gates
  const changeGatesWithRoute = useCallback((gates: NullableIGate[], route: IRoute) => {
    const newGates = gates.map((gate, index) => ({
      ...gate,
      coordinate: route.coordinates[index] || { latitude: null, longitude: null },
    }));
    if (gates.length < route.coordinates.length) {
      for (let i = gates.length; i < route.coordinates.length; i++) {
        newGates.push({
          id: 0,
          sequence: i,
          time: null,
          coordinate: {
            latitude: route.coordinates[i].latitude,
            longitude: route.coordinates[i].longitude,
          },
          traveledDistance: null,
        });
      }
    }
    return newGates;
  }, []);

  // 입력을 위한 새로운 gate 생성
  const handleAddNewGate = useCallback(() => {
    setUpdateGatesState((prev) => [...prev, createGateWithIndex(prev.length)]);
  }, []);

  // 마지막 빈 gate 추가
  const createGateWithIndex = useCallback((index: number) => {
    return {
      id: 0,
      sequence: index,
      time: null,
      coordinate: {
        latitude: null,
        longitude: null,
      },
      traveledDistance: null,
    };
  }, []);

  // gates -> route, routes (for useEffect)
  const changeRouteRoutesWithGates = useCallback(
    (gates: NullableIGate[]) => {
      const updatedCoordinates = gates.map((gate) => gate.coordinate);

      // updateRouteState의 타입을 IRoute로 일치시킴
      if (updateRouteState) {
        const newUpdateRouteState: IRoute = {
          ...updateRouteState,
          coordinates: updatedCoordinates as Coordinate[], // 타입 명시적 변환
        };
        setUpdateRouteState(newUpdateRouteState);

        // updateRoutesState의 각 route도 IRoute 타입으로 일치시킴
        const newUpdateRoutesState = updateRoutesState?.map((route) =>
          route.id === newUpdateRouteState.id ? { ...route, coordinates: updatedCoordinates as Coordinate[] } : route,
        );
        setUpdateRoutesState(newUpdateRoutesState);
      }
    },
    [updateRouteState, updateRoutesState],
  );

  // gate 삭제 버튼
  const handleRemoveGate = useCallback((index: number) => {
    setUpdateGatesState((prev) => prev.filter((_, i) => i !== index));
  }, []);

  return (
    <Content style={{ height: 'calc((100vh - 126px) / 2)' }}>
      <Content style={{ height: '120px' }}>
        <ContentLine style={{ justifyContent: 'space-between', alignItems: 'center' }}>
          {isModify && <CustomTextButton onClick={handleAddNewGate}>Add New Gate</CustomTextButton>}
          <SelectOption
            style={{ margin: '0 auto' }}
            value={updateRouteState?.id || 'createRoute'}
            onChange={changeRoute}
            disabled={!isModify}
          >
            {updateRoutesState.map((route, index) => (
              <option key={index} value={route.id}>
                {route.name}
              </option>
            ))}
            <option value={'createRoute'}>Change Route Directly</option>
          </SelectOption>
          <div style={{ display: 'inline-flex', gap: '10px' }}>
            {isModify ? (
              <CustomTextButton onClick={() => onSaveGateList()}>Save</CustomTextButton>
            ) : (
              <CustomTextButton onClick={() => onChangeToModify()}>Modify</CustomTextButton>
            )}
          </div>
        </ContentLine>
        <ContentLine>
          <ContentLineText className="width70px">
            Gate
            <br />
            Number
          </ContentLineText>
          <ContentLineText className="width120px">
            Time
            <br />
            MMDD hhmmss
          </ContentLineText>
          <ContentLineText className="flex">Latitude</ContentLineText>
          <ContentLineText className="flex">Longitude</ContentLineText>
          <CustomIconButton style={{ visibility: 'hidden' }}>X</CustomIconButton>
          <div className="width6px"></div>
        </ContentLine>
      </Content>
      <ScrollZone style={{ height: 'calc((100vh - 126px) / 2) - 120px' }}>
        <Scrollbars>
          <div id="sortable-gates">
            {updateGatesState.map((updateGateState, index) => (
              <EachGate
                key={updateGateState.id}
                keyValue={updateGateState.sequence}
                gateState={updateGateState}
                isModify={isModify}
                onGateChange={(updatedGate) => handleGateChange(index, updatedGate)}
                handleRemoveGate={() => handleRemoveGate(index)}
              />
            ))}
          </div>
        </Scrollbars>
      </ScrollZone>
    </Content>
  );
};

export default GateList;
