import React, { useCallback, useEffect, useState } from 'react';
import { Content, ContentLine, ContentLineText } from '@components/Playbar/styles';
import { IGate, Location, SectionData, SpeedPredictionInterval } from '@typings/db';
import { convertLocationToString } from '@utils/enumUtil';
import useCustomGates from '@hooks/useCustomGates';
import useCustomPlaybar from '@hooks/useCustomPlaybar';
import {
  calculateDistance,
  calculateSpeedKmPerHour,
  calculateTime,
  formatToTwoDecimalPlaces,
  getGateByLocation,
} from '@utils/displayUtil';
import { formatTimeToHHMMSS } from '@utils/dateUtil';
import { TestButton } from '@components/Map/styles';

interface EachDisplayDataProps {
  keyValue: number;
  start: Location;
  end: Location;
  sectionDatas: SectionData[];
  speedPredictionInterval: SpeedPredictionInterval;
}

const EachDisplay: React.FC<EachDisplayDataProps> = ({
  keyValue,
  start,
  end,
  sectionDatas,
  speedPredictionInterval,
}) => {
  const { gatesState } = useCustomGates();
  const { playbarState } = useCustomPlaybar();
  const [startGate, setStartGate] = useState<IGate | null>(null);
  const [endGate, setEndGate] = useState<IGate | null>(null);
  const [distance, setDistance] = useState<number | null>(null);
  const [elaspedTime, setElaspedTime] = useState<number | null>(null);
  const [estimatedTime, setEstimatedTime] = useState<number | null>(null);
  const [elaspedSpeed, setElaspedSpeed] = useState<number | null>(null);
  const [estimatedSpeed, setEstimatedSpeed] = useState<number | null>(null);

  useEffect(() => {
    setStartGate(getGateByLocation(playbarState, start));
    setEndGate(getGateByLocation(playbarState, end));
    // 거리
    const newDistance = calculateDistance(startGate, endGate);
    setDistance(newDistance);
    // 걸린 시간
    const newElaspedTime = calculateTime(startGate, endGate);
    setElaspedTime(newElaspedTime);
    // 속도
    const newElaspedSpeed = calculateSpeedKmPerHour(startGate, endGate);
    setElaspedSpeed(newElaspedSpeed);

    // 추정 속도 <- playbarState
    let endTime: string | null = null;
    let startTime: string | null = null;
    let distanceForEstimating: number | null = null;
    if (speedPredictionInterval == SpeedPredictionInterval.FIRST) {
      if (playbarState?.firstGate?.time && playbarState?.latestGateBasedOnCurrent?.time) {
        startTime = playbarState?.firstGate?.time;
        endTime = playbarState?.latestGateBasedOnCurrent?.time;
        distanceForEstimating = calculateDistance(playbarState?.firstGate, playbarState?.latestGateBasedOnCurrent);
      }
    } else if (speedPredictionInterval == SpeedPredictionInterval.PREVIOUS) {
      if (playbarState?.previousGateBasedOnCurrent?.time && playbarState?.latestGateBasedOnCurrent?.time) {
        startTime = playbarState?.previousGateBasedOnCurrent?.time;
        endTime = playbarState?.latestGateBasedOnCurrent?.time;
        distanceForEstimating = calculateDistance(
          playbarState?.previousGateBasedOnCurrent,
          playbarState?.latestGateBasedOnCurrent,
        );
      }
    }
    if (endTime && startTime && distanceForEstimating) {
      const newEstimatedSpeed = distanceForEstimating / (new Date(endTime).getTime() - new Date(startTime).getTime());
      setEstimatedSpeed(3600 * newEstimatedSpeed); // m/ms -> km/h 변환
      // 추정 시간 <- 거리 / 추정 속도
      if (distance) {
        const newEstimatedTime = distance / newEstimatedSpeed;
        setEstimatedTime(newEstimatedTime);
      }
    }
  }, [start, end, gatesState, playbarState, startGate, endGate, speedPredictionInterval, distance]);

  const handleTest = useCallback(() => {}, []);

  return (
    <Content>
      {keyValue === 3 && <TestButton onClick={handleTest}></TestButton>}
      <ContentLine>
        <ContentLineText className="width300px">
          {convertLocationToString(start)} ~ {convertLocationToString(end)}
        </ContentLineText>
        {sectionDatas.map((data, index) => (
          <ContentLineText key={`${data}-${index}`} className="flex">
            {data === SectionData.DISTANCE && distance !== null ? formatToTwoDecimalPlaces(distance) : ''}
            {data === SectionData.ELAPSED_TIME && elaspedTime !== null ? formatTimeToHHMMSS(elaspedTime) : ''}
            {data === SectionData.ESTIMATED_TIME && estimatedTime !== null ? formatTimeToHHMMSS(estimatedTime) : ''}
            {data === SectionData.ELAPSED_SPEED && elaspedSpeed !== null ? formatToTwoDecimalPlaces(elaspedSpeed) : ''}
            {data === SectionData.ESTIMATED_SPEED && estimatedSpeed !== null
              ? formatToTwoDecimalPlaces(estimatedSpeed)
              : ''}
          </ContentLineText>
        ))}
      </ContentLine>
    </Content>
  );
};

export default EachDisplay;
